<template>
  <div class="container">
    <h1>DAYS WITHOUT</h1>
    <img :src="require(`@/assets/lol.png`)" alt="image" />
    <div id="counter">0</div>
    <div>
      <div class="button-container" id="increase">
        <button class="button-74" @click="increaseCounter">INCREASE</button>
      </div>
      <div class="button-container" id="reset">
        <button class="button-74 reset" @click="resetCounter">RESET</button>
      </div>
    </div>
  </div>
</template>

<script>
let getCounterFromLocalStorage = () => {
  return parseInt(
    localStorage.getItem("league-of-legends-days-off-counter") || 0
  );
};
let setCounter = (value) => {
  localStorage.setItem("league-of-legends-days-off-counter", value);
};
let refreshContent = () => {
  document.getElementById("counter").innerHTML = getCounterFromLocalStorage();
};

export default {
  name: "Home",
  mounted: () => {
    refreshContent(getCounterFromLocalStorage());
  },
  methods: {
    increaseCounter: () => {
      setCounter(getCounterFromLocalStorage() + 1);
      refreshContent();
    },
    resetCounter: () => {
      setCounter(0);
      refreshContent();
    },
  },
};
</script>

<style scoped>
h1 {
    letter-spacing: 3px;
}
.container {
  text-align: center;
}
.button-container {
  margin-bottom: 10px;
}
img {
  max-width: 500px;
}
#counter {
  font-size: 5em;
  font-weight: bold;
  margin-bottom: 50px;
}

.button-74 {
  background-color: #e9c46a;
  border: 2px solid #f4a261;
  border-radius: 30px;
  box-shadow: #e76f51 4px 4px 0 0;
  color: #264653;
  cursor: pointer;
  display: inline-block;
  font-weight: 600;
  font-size: 18px;
  padding: 0 18px;
  line-height: 50px;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-74:hover {
  background-color: #f7ede2;
}

.button-74:active {
  box-shadow: #422800 2px 2px 0 0;
  transform: translate(2px, 2px);
}

@media (min-width: 768px) {
  .button-74 {
    min-width: 120px;
    padding: 0 25px;
  }
}
</style>